html, body, section, article, h1, h2, p, strong, em, a {
  margin: 0;
  border: 0;
  padding: 0;
  outline: 0;

  box-sizing: inherit;
  font: inherit;
  text-decoration: inherit;
  text-align: inherit;
  color: inherit;

  background: transparent;
}

body{
    font-family: 'Montserrat', sans-serif;
    background-color: #12161f;
    line-height: 1.5;
    color: white;
    display: block;
    background-image: url(./welcome_bg.jpeg);
}

main{
